
.bonus{
	position:absolute;
	min-width:1000px;
	width:100%;
	height:500%;
	left:0;
	z-index:100;
	background-color:#f3f3f3;
	-background-color:blue;
}
.bonus-header{
	padding-top:36px;
	padding-bottom:30px;
}
.bonus-header h2{
	text-align:center;
	font-size:34px;
	font-weight:normal;
	color:#000000;
}
.bonus-header h2:before,.bonus-header h2:after{
	content:'';
	display:inline-block;
	width:50px;
	height:1px;
	background-color:#d5d5d5;
	margin-right:10px;
	margin-left:10px;
	position:relative;
	top:-8px;
}
.bonus-header h2 span{
	color:#fb7f6e;
}
.bonus-share{
	overflow:hidden;
	text-align:center;
}
.bonus-share ul{
	display:inline-block;
	margin:0 auto;
}
.bonus-share li{
	float:left;
	margin:0 10px 0 15px;
}
.bonus-share li .fa{
	padding-right:8px;
}
.bonus-content {
	margin-top:20px;
}
.bonus-content ul{
	overflow:hidden;
}
.bonus-content li{
	width:322px;
	height:660px;
	/*background:url('../../static/bonus_1.jpg') 0% 0% / 100% no-repeat #ffffff;让图片自动适应宽高 不需要ps修改图片大小了*/
	margin-right:10px;
	float:left;
	position:relative;
	margin-bottom:20px;
	box-shadow: 5px 5px 10px #d5d5d5;
}
.detail {
	padding-top:270px;
}
.detail h3{
	text-align:center;
	font-size:14px;
}
.detail .line{
	text-align:center;
	width:50px;
	height:1px;
	background:#d5d5d5;
	margin:20px auto 20px auto;
}
.detail p{
	text-align:center;
	line-height:20px;
	font-size:12px;
}
.mask{
	position:absolute;
	background-color: #efefef;
	width:342px;
	height:660px;
	left:0;
	top:0;
	opacity:0;
	text-align:center;
}
.mask:hover{
	opacity:0.8;
}
.mask span{
	background-color:#F5674C;
	display:inline-block;
	width:100px;
	height:32px;
	border-radius:5px;
	line-height:32px;
	color:#ffffff;
	margin-top:292px;
}
.mask:hover{
	cursor:pointer;
}
.goback{
	text-align:center;
	padding-top:10px;
	padding-bottom:40px;
}



