.article-page{
	margin-left:200px;
	overflow:hidden;
}
.article-page nav{
	height:50px;
}
.article-page nav ul{
	float:left;
	overflow:hidden;
	padding-left:10px;
}
.article-page nav ul li{
	float:left;
	font-size:12px;
	height:49px;
	line-height:49px;
	margin:0 5px;
}
.article-page nav ul li.active{
	border-bottom:1px solid  #000000;
}
.article-page nav ul li a{
	float:left;
	padding:0 15px;
}
.article-page nav ul li a:hover{
	background-color:#eeeeee;
}
.article-page .search{
	overflow:hidden;
	float:right;
	padding-top:15px;
	padding-right:10px;
	position:relative;
}
.article-page .search input{
	float:left;
	height:26px;
	line-height:26px;
	width:140px;
	padding:0 20px 0 10px;
	border-radius:13px;
	outline:none;
	border:0;
	font-size:14px;
}
.article-page .search input:focus{
	border:1px solid #74b9ef;
}
.article-page .search .fa-search{
	position:absolute;
	right:18px;
	top:20px;
}
.article-list .taglist{
	overflow:hidden;
	-height:40px;
	padding:10px 15px;
}
.article-list .taglist li{
	float:left;
	margin:5px;
}
.article-list .taglist li a{
	float:left;
	padding:5px 10px;
	font-size:12px;
	border:1px solid #d9d9d9;
	border-radius:8px;
}
.article-list .taglist li a.active{
	background-color:#de533a;
	color:white;
}



