
.article-cont{
	height:auto;
}
.article-cont ul{
	overflow:hidden;
}
.article-cont li {
	position:relative;
	padding-left:20px;
	border-bottom:1px dashed #d9d9d9;
}
.article-cont li img{
	width:100px;
	height:100px;
	position:absolute;
	right:10px;
	top:10px;
	border-radius:5px;
}
.author-time{
	padding-top:20px;
	font-size:12px;
	line-height:2;
}
.author-time a{
	color:#56b7df;
}
.author-time span{
	color:#717171;
}
.article-title{
	height:36px;
	color:#555555;
	line-height:2;
	font-size:18px;
}
.article-comment{
	color:#717171;
	font-size:12px;
	line-height:1.5;
	padding-bottom:20px;
}



