.topic-cont{
	margin-left:200px;
	overflow:hidden;
}
.topic-cont nav{
	height:50px;
	border-bottom: 1px solid #efefef;
}
.topic-cont nav ul{
	float:left;
	overflow:hidden;
	padding-left:10px;
}
.topic-cont nav ul li{
	float:left;
	font-size:12px;
	height:49px;
	line-height:49px;
	margin:0 5px;
}
.topic-cont nav ul li.active{
	border-bottom:1px solid  #000000;
}
.topic-cont nav ul li a{
	float:left;
	padding:0 15px;
}
.topic-cont nav ul li a:hover{
	background-color:#eeeeee;
}
.topic-cont .search{
	overflow:hidden;
	float:right;
	padding-top:15px;
	padding-right:10px;
	position:relative;
}
.topic-cont .search input{
	float:left;
	height:26px;
	line-height:26px;
	width:140px;
	padding:0 20px 0 10px;
	border-radius:13px;
	outline:none;
	border:0;
	font-size:14px;
}
.topic-cont .search input:focus{
	border:1px solid #74b9ef;
}
.topic-cont .search .fa-search{
	position:absolute;
	right:18px;
	top:20px;
}
.sort{
	padding-left:25px;
	padding-top:30px;
	font-size:14px;
}
.topic-list ul{
	padding-top:30px;
}
.topic-list li{
	width:580px;
	margin-left:20px;
	overflow:hidden;	
	border-bottom: 1px dashed #d9d9d9;
	padding-bottom:5px;
	margin-bottom:20px;
}
.topic-img{
	width:40px;
	height:40px;
	border-radius:50%;
	border: 2px solid #fff;
	float:left;
	margin-left:3px;
}
.topic-detail{
	float:left;
	width:356px;	
	padding-left:15px;
}
.topic-detail h3{
	font-size:14px;
}
.topic-text{
	font-size:12px;
	color:#999999;
	padding-top:5px;
	padding-bottom:5px;
}
.topic-tag-word{
	font-size:12px;
	color:#999999;
	padding-top:5px;
	padding-bottom:5px;
}
.amount{
	color:#4094c7;
}
.topic-input{
	float:right;
	margin-top:30px;
	margin-right:5px;
}
.topic-input button{
	width:100px;
	height:26px;
	background-color:#49be38;
	border-radius:13px;
	outline:none;
	border:0;
	line-height:26px;
	color:#fff;
}




